@font-face {
  font-family: 'Roboto-Bold';
  src: url('./assets/fonts/Roboto-Bold/Roboto-Bold.eot');
  src: local('☺'),
    url('./assets/fonts/Roboto-Bold/Roboto-Bold.woff') format('woff'),
    url('./assets/fonts/Roboto-Bold/Roboto-Bold.ttf') format('truetype'),
    url('./assets/fonts/Roboto-Bold/Roboto-Bold.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto-Medium';
  src: url('./assets/fonts/Roboto-Medium/Roboto-Medium.eot');
  src: local('☺'),
    url('./assets/fonts/Roboto-Medium/Roboto-Medium.woff') format('woff'),
    url('./assets/fonts/Roboto-Medium/Roboto-Medium.ttf') format('truetype'),
    url('./assets/fonts/Roboto-Medium/Roboto-Medium.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto-Light';
  src: url('./assets/fonts/Roboto-Light/Roboto-Light.eot');
  src: local('☺'),
    url('./assets/fonts/Roboto-Light/Roboto-Light.woff') format('woff'),
    url('./assets/fonts/Roboto-Light/Roboto-Light.ttf') format('truetype'),
    url('./assets/fonts/Roboto-Light/Roboto-Light.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  font-family: 'Roboto-Light';
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 300;
}

#root,
.App {
  height: 100%;
}

[class*='__container'] {
  max-width: 1440px;
  margin: 0 auto;
}
